.btn_border {
  display: inline-block;
  padding: 15px 35px;
  font-family: var(--third-family);
font-weight: 500;
font-size: 16px;
line-height: 100%;
text-transform: uppercase;
color: $main;
position: relative;
border: 2px solid transparent;
}


.btn_border::before, .btn_border::after, .btn_border span::before, .btn_border span::after {
  display: block;
  content: "";
  width: 13px;
  height: 13px;
  position: absolute;
}

.btn_border::before {
  top: -1px;
  left: -1px;
  border-top: 2px solid $red;
  border-left: 2px solid $red;
}
.btn_border::after {
  top: -1px;
  right: -1px;
  border-top: 2px solid $red;
  border-right: 2px solid $red;
}
.btn_border span::before {
  bottom: -1px;
  left: -1px;
  border-bottom: 2px solid $red;
  border-left: 2px solid $red;
}
.btn_border span::after {
  bottom: -1px;
  right: -1px;
  border-bottom: 2px solid $red;
  border-right: 2px solid $red;
}


.btn_border:hover {
  border-color: $redhover;
  background: $redhover;
  color: #fff;
  &::before {
    border-top: 2px solid $redhover;
    border-left: 2px solid $redhover;
  }
  &::after {
    border-top: 2px solid $redhover;
    border-right: 2px solid $redhover;
  }
  & span::before {
    border-bottom: 2px solid $redhover;
    border-left: 2px solid $redhover;
  }
  &  span::after {
    border-bottom: 2px solid $redhover;
    border-right: 2px solid $redhover;
  }
}
.btn_border:focus {
  border-color: $redfocus;
  background: $redfocus;

  &::before {
 
    border-top: 2px solid $redfocus;
    border-left: 2px solid $redfocus;
  }
  &::after {

    border-top: 2px solid $redfocus;
    border-right: 2px solid $redfocus;
  }
  & span::before {

    border-bottom: 2px solid $redfocus;
    border-left: 2px solid $redfocus;
  }
  & span::after {
 
    border-bottom: 2px solid $redfocus;
    border-right: 2px solid $redfocus;
  }
}

